<template>
  <section>
    <div class="container">
      <form class="form-control">
        <div class="d-col">
          <div class="wrap jcc">
          <div class="col-input">
            <p class="addPost">Add Post</p>
          </div>
        </div>
        <div class="wrap jcc">
          <div class="col-input">
            <vs-input border class="input-flex" placeholder="Name" v-model="Post.name"></vs-input>
          </div>
        </div>
        <div class="wrap jcc">
          <div class="col-input">
            <vs-input border class="input-flex" placeholder="Email" v-model="Post.email"></vs-input>
          </div>
        </div>
        <div class="wrap jcc">
          <div class="col-input">
            <textarea class="textarea-input" type="text" placeholder="Description" required="required" v-model="Post.description"></textarea>
          </div>
        </div>
        <div class="wrap jcc">
          <div class="col-input">
            <vs-input border class="input-flex" placeholder="Package" v-model="Post.pachet"></vs-input>
          </div>
        </div>
        <div class="wrap jcc">
          <div class="col-input">
            <vs-button
              class="btn-post"
              gradient
              @click="addToAPI()"
            >
              Add Post
            </vs-button>
          </div>
        </div>
        </div>
      </form>
    </div>
  </section>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      Post: { name: "", email: "", description: "", pachet: "" },
    };
  },
  methods: {
    addToAPI() {
      let newPost = {
        name: this.Post.name,
        email: this.Post.email,
        description: this.Post.description,
        pachet: this.Post.pachet,
      };
      console.log(newPost);
      axios
        .post("https://charitablebedatabase.herokuapp.com/posts", newPost)
        .then((response) => {
          console.log(response);
        })
        .catch((error) => {
          console.log(error);
      });
      this.$router.push('/')
      window.location.reload()
    },
  },
};
</script>

